/** @jsx jsx */
import { jsx } from '@emotion/core';
import Heading from '@chakra-ui/core/dist/Heading';
import Box from '@chakra-ui/core/dist/Box';
import Text from '@chakra-ui/core/dist/Text';
import Flex from '@chakra-ui/core/dist/Flex';
import Badge from '@chakra-ui/core/dist/Badge';
import { formatDate, nl2br } from '~/utils';
import SectionTitle from '../SectionTitle';
import Section from '../Section';

export default function Promo({ data, ...props }) {
  return (
    <Section {...props}>
      <Box mx={[2, 8, 24]}>
        <SectionTitle>Promozioni</SectionTitle>
        {data.map((item) => {
          const description = nl2br(item.descrizione);
          const scadenza = formatDate(item.scadenza);

          return (
            <Box p={4} key={item.scadenza} as="article" minHeight="12em">
              <Flex align="baseline" mb={1}>
                <Badge variantColor="green">{item.area}</Badge>
                <Text fontSize="sm" fontWeight="semibold" color="green.800" ml={1}>
                  &bull; Valida fino al {scadenza}
                </Text>
              </Flex>
              <Heading size="sm" mb={2} lineHeight="short">
                {item.titolo}
              </Heading>
              <Flex
                borderRadius="50%"
                w="92px"
                h="92px"
                backgroundColor="green.100"
                align="center"
                justify="center"
                float="left"
                m={2}
                css={{ shapeOutside: 'circle()' }}
              >
                <img
                  src={`/static/images/partners/${item.produttore.toLowerCase()}.png`}
                  width="92px"
                  alt={item.produttore}
                  loading="lazy"
                />
              </Flex>
              <Text fontSize="sm" dangerouslySetInnerHTML={{ __html: description }} />
            </Box>
          );
        })}
      </Box>
    </Section>
  );
}
